import {
  Anchor,
  Button,
  Checkbox,
  Divider,
  Input,
  Layout,
  Popconfirm,
  Radio,
  Select,
  Space,
  Typography,
  message,
} from "antd";
import { AnchorContainer } from "antd/es/anchor/Anchor";
import React, { useState } from "react";
import IconFont from "../../components/iconfont";
import { Container, Panel } from "./style";

const { TextArea } = Input;
const { Title, Text } = Typography;
const { Sider, Content } = Layout;

export default function Setting() {
  const [advice, setAdvice] = useState("");
  const [messageApi, contextHolder] = message.useMessage();

  const success = (message: string = "") => {
    messageApi.open({
      type: "success",
      content: message,
    });
  };

  const openBackupMessage = () => {
    messageApi.open({
      key: "backup",
      type: "loading",
      content: "...",
    });
    setTimeout(() => {
      messageApi.open({
        key: "backup",
        type: "success",
        content: "",
      });
    }, 2000);
  };

  return (
    <>
      {contextHolder}
      <Panel>
        <Title level={3}></Title>
        <Container id="setting-container">
          <Sider
            theme="light"
            style={{ position: "sticky", overflowY: "auto", top: 0 }}
          >
            <div style={{ marginTop: 20 }}>
              <Anchor
                onClick={(ev) => ev.preventDefault()}
                getContainer={() =>
                  document.getElementById(
                    "setting-container"
                  ) as AnchorContainer
                }
                affix={false}
                items={[
                  {
                    key: "account",
                    href: "#setting-account",
                    title: (
                      <Space>
                        <IconFont type="icon-account" />
                        <Text></Text>
                      </Space>
                    ),
                  },
                  {
                    key: "apperance",
                    href: "#setting-appearance",
                    title: (
                      <Space>
                        <IconFont
                          type="icon-appearance"
                          style={{ fontSize: 15 }}
                        />
                        <Text></Text>
                      </Space>
                    ),
                  },
                  {
                    key: "language",
                    href: "#setting-language",
                    title: (
                      <Space>
                        <IconFont
                          type="icon-language"
                          style={{ fontSize: 15 }}
                        />
                        <Text></Text>
                      </Space>
                    ),
                  },
                  {
                    key: "backup",
                    href: "#setting-backup",
                    title: (
                      <Space>
                        <IconFont type="icon-backup" style={{ fontSize: 15 }} />
                        <Text></Text>
                      </Space>
                    ),
                  },
                  {
                    key: "advice",
                    href: "#setting-advice",
                    title: (
                      <Space>
                        <IconFont type="icon-advice" style={{ fontSize: 15 }} />
                        <Text></Text>
                      </Space>
                    ),
                  },
                  {
                    key: "aboutus",
                    href: "#setting-aboutus",
                    title: (
                      <Space>
                        <IconFont
                          type="icon-aboutus"
                          style={{ fontSize: 15 }}
                        />
                        <Text></Text>
                      </Space>
                    ),
                  },
                ]}
              />
            </div>
          </Sider>
          <Content style={{ marginLeft: 15 }}>
            <Title level={5} style={{ marginTop: 0 }} id="setting-account">
              
            </Title>
            <div style={{ marginTop: 15 }}>
              <Space direction="vertical" size={15}>
                <Space>
                  <Button type="primary"></Button>
                  <Text style={{ color: "#000000a6" }}></Text>
                  <Text
                    style={{ color: "#000000a6", cursor: "pointer" }}
                    underline
                  >
                    
                  </Text>
                </Space>
              </Space>
            </div>
            <Divider />
            <Title level={5} id="setting-appearance">
              
            </Title>
            <div style={{ marginTop: 15 }}>
              <Space direction="vertical" size={15}>
                <Radio.Group
                  defaultValue=""
                  buttonStyle="solid"
                  onChange={() => success("")}
                >
                  <Radio.Button value=""></Radio.Button>
                  <Radio.Button value=""></Radio.Button>
                  <Radio.Button value=""></Radio.Button>
                </Radio.Group>
              </Space>
            </div>
            <Divider />
            <Title level={5} id="setting-language">
              
            </Title>
            <div style={{ marginTop: 15 }}>
              <Space direction="vertical" size={15}>
                <Select
                  defaultValue="zh-CN"
                  style={{ width: 120 }}
                  onChange={() => success("")}
                  options={[
                    { value: "zh-CN", label: "" },
                    { value: "en-US", label: "English" },
                  ]}
                />
              </Space>
            </div>
            <Divider />
            <Title level={5} id="setting-backup">
              
            </Title>
            <div style={{ marginTop: 15 }}>
              <Space direction="vertical" size={15}>
                <Space>
                  <Button type="primary" onClick={() => openBackupMessage()}>
                    
                  </Button>
                  <Text style={{ color: "#000000a6" }}>
                    2023.08.05 12:23:11
                  </Text>
                </Space>
              </Space>
            </div>
            <Divider />
            <Title level={5} id="setting-advice">
              
            </Title>
            <div style={{ marginTop: 15 }}>
              <Space direction="vertical" size={15}>
                <TextArea
                  rows={4}
                  cols={60}
                  showCount
                  allowClear
                  style={{ height: 120, resize: "none" }}
                  placeholder="10"
                  value={advice}
                  onChange={(ev) => {
                    setAdvice(ev.target.value);
                  }}
                />
                <Space>
                  <Button
                    type="primary"
                    disabled={advice.length < 10}
                    onClick={() => {
                      setAdvice("");
                      success(" 7 ");
                    }}
                  >
                    
                  </Button>
                  <Text style={{ color: "#000000a6" }}></Text>

                  <Popconfirm
                    placement="topLeft"
                    title=""
                    description={<Collect />}
                    onConfirm={() => {}}
                    okText=""
                    cancelText=""
                  >
                    <Text
                      style={{ color: "#000000a6", cursor: "pointer" }}
                      underline
                    >
                      
                    </Text>
                  </Popconfirm>
                </Space>
              </Space>
            </div>
            <Divider />
            <Title level={5} id="setting-aboutus">
              
            </Title>
            <div style={{ marginTop: 15 }}>
              <Space direction="vertical" size={15}>
                <Text style={{ color: "#000000a6" }}>
                   .NET/C# 
                </Text>
              </Space>
            </div>
            <Divider />
          </Content>
        </Container>
      </Panel>
    </>
  );
}

const Collect: React.FC = () => {
  return (
    <div style={{ padding: "10px 20px 10px 0" }}>
      <Space direction="vertical">
        <Checkbox defaultChecked> (/MAC/IP)</Checkbox>
        <Checkbox defaultChecked> ()</Checkbox>
        <Checkbox defaultChecked> (//)</Checkbox>
        <Checkbox defaultChecked disabled>
           (.NET SDK/Furion)
        </Checkbox>
      </Space>
    </div>
  );
};
